.reviews__title {
  .section-header ();
  margin-bottom: 24px;
}

.reviews__list {
  .list-reset();
  margin-bottom: 29px;
}

.reviews__text {
  .font (15px; 30px);
  .weight (400);
  color: @color-greyish-brown;
  margin: 0 0 27px 0;
}

.reviews__author-name {
  .font(17px; 24px);
  font-style: normal;
}

.reviews__author-nickname {
  .font(17px; 30px);
  .weight(400);
  color: @color-greyish-brown;
  margin: 3px 0 0 0;
}

.reviews__control {
  display: flex;
}

.reviews__button {
  background-color: @color-white;
  flex-grow: 1;
  height: 52px;
  border: 1px solid @base-color;
  margin-bottom: 38px;
  position: relative;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.reviews__write {
  .font(18px; 20px);
  .link-reset (@color-greyish-brown);
  text-align: center;
  display: block;
}

@media (min-width: @tablet-width) {
  .reviews {
    position: relative;

    &::before {
      content: "";
      display: block;
      .size (350px; 457px);
      background-image: url("../../img/index/review@1x.jpg");
      background-repeat: no-repeat;
      position: absolute;
      z-index: -1;
      top: 0;
      left: -49px;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../../img/retina/review@2x.jpg");
      }
    }

    &::after {
      content: "";
      display: block;
      background-image: url("../../img/index/bg-zigzag-line.svg");
      background-repeat: repeat-x;
      width: 100%;
      height: 23px;
      position: absolute;
      bottom: -135px;
    }
  }

  .reviews__title {
    .font (43px; 50px);
    padding-top: 77px;
    margin-left: -4px;
    margin-bottom: 49px;
  }

  .reviews__text {
    font-size: 17px;
    margin-bottom: 57px;
  }

  .reviews__author-name {
    font-size: 18px;
  }

  .reviews__control {
    position: absolute;
    bottom: -36px;
    right: 0;
  }

  .reviews__button {
    .size (89px; 52px);
    border: none;
  }

  .reviews__button--prev::before,
  .reviews__button--next::before {
    content: "";
    left: 23px;
  }

  .reviews__write {
    width: fit-content;
    padding: 14px 35px;
    position: absolute;
    top: 79px;
    right: 1px;
  }
}

@media (min-width: @desktop-width) {
  .reviews {
    &::before {
      content: "";
      left: 0;
    }

    &::after {
      content: "";
      left: 0;
      bottom: -185px;
    }
  }

  .reviews__title {
    .font (53px; 57px);
    padding-top: 70px;
  }

  .reviews__text {
    margin-right: 106px;
    margin-bottom: 40px;
  }
}
